class FacilitySelectorController {
    static getCheckedItemFlatList(checkedItemMap) {
        return Array.from(checkedItemMap.keys()).map(key => checkedItemMap.get(key).checkedPath.find((item) => item.id === key));
    }
    static getCheckedItemListPerLevel(checkedItemMap) {
        const checkedItemList = [];
        Array.from(checkedItemMap.keys()).forEach(key => {
            const checkedItem = checkedItemMap.get(key);
            if (!checkedItemList[checkedItem.levelIndex]) {
                checkedItemList[checkedItem.levelIndex] = [];
            }
            checkedItemList[checkedItem.levelIndex].push(checkedItem.checkedPath.find((item) => item.id === key));
        });
        for (let i = 0; i < checkedItemList.length; i++) {
            if (!checkedItemList[i]) {
                checkedItemList[i] = [];
            }
        }
        return checkedItemList;
    }
    static getSelectionPath(checkedItemMap) {
        const checkedPaths = Array.from(checkedItemMap.values()).map((item) => item.checkedPath);
        const selectionPath = []; // holds all selections for a level
        const deepestPath = [];
        let i = 0;
        checkedPaths.forEach((path) => {
            path.forEach((item, index) => {
                if (!selectionPath[index]) {
                    selectionPath[index] = [];
                }
                if (selectionPath[index].filter((s) => s.id === item.id).length > 0) {
                    return;
                }
                selectionPath[index].push(item);
            });
        });
        if (selectionPath.length > 0) {
            while (selectionPath[i].length === 1 && i < selectionPath.length - 1) {
                deepestPath.push(...selectionPath[i]);
                i++;
            }
        }
        return [...deepestPath];
    }
    static getIndeterminateAndCheckedItems(checkedItemMap) {
        const indeterminateItemArray = [];
        const checkedItemArray = [];
        // Populating array of indeterminate items & checked items
        Array.from(checkedItemMap.keys()).forEach((key) => {
            const checkedItem = checkedItemMap.get(key);
            const levelIndex = checkedItem.levelIndex;
            checkedItem.checkedPath.forEach((pathItem, i) => {
                if (pathItem.id !== key) {
                    if (!indeterminateItemArray[i]) {
                        indeterminateItemArray[i] = [];
                    }
                    if (indeterminateItemArray[i].filter(item => item.id === pathItem.id).length <= 0) {
                        indeterminateItemArray[i].push(pathItem);
                    }
                }
                else if (pathItem.id === key) {
                    if (!checkedItemArray[levelIndex]) {
                        checkedItemArray[levelIndex] = [];
                    }
                    checkedItemArray[levelIndex].push(pathItem);
                }
            });
        });
        // Populating empty array indexes
        for (let i = 0; i < checkedItemArray.length; i++) {
            if (!checkedItemArray[i]) {
                checkedItemArray[i] = [];
            }
        }
        return {
            indeterminateItemArray,
            checkedItemArray
        };
    }
}

export { FacilitySelectorController as F };

